<template>
	<div class="container" style="background:#f7f8f9; margin:10px auto;">
		<!-- <div class="video-container">
			<my-video :sources="video.sources" :options="video.options"></my-video>
		</div> -->
		<div class="panel panel-default">
	        <div class="panel-body panel-default">
	        	<div class="titleContainer">
	        		<span class="title"><span class="iconfont-h2">免费</span>直播讲座</span>
	        	</div>
	        </div>
	        <div class="row">
	          <div v-for="teacher in teachers" class="col-sm-6 col-md-4">
	            <div class="thumbnail">
	              <img class="teacher" :src="teacher.avatar" alt="...">
	              <div class="caption">
	                <p class="class-title">{{teacher.lecture.name}}</p>
	                <p class="des">时间：{{teacher.lecture.date}}</p>
	                <p class="des">时长：{{teacher.lecture.time}}</p>
	                <p class="des">讲师：{{teacher.name}}</p>
	                <p style="text-align:center;">
	                  <button class="btn btn-primary" v-on:click="showSummary(teacher)">{{btnTitle}}</button>
	                  <div id="qqg" v-if="isZhankai">
	                    <hr>
	                    <p>{{teacher.introduction}}</p>
	                  </div>
	                </p>
	              </div>
	            </div>
	          </div>
	        </div>
      </div>
	</div>

</template>

<script>
	import myVideo from 'vue-video'
	export default{
		name:"teachers",
		data(){
			return{
				teachers:[],
				btnTitle:"展开简介",
			    isZhankai:false,
			    video: {
	                sources: [{
	                    src: 'https://video-1253468238.cos.ap-beijing.myqcloud.com/%E6%B8%A9%E7%97%85%E7%AC%AC%E4%B8%80%E8%8A%82.mp4?sign=q-sign-algorithm%3Dsha1%26q-ak%3DAKIDbcW0XTqrrtG7o0qwX1HUJBA7pfdWOrY6%26q-sign-time%3D1520921628%3B1520923428%26q-key-time%3D1520921628%3B1520923428%26q-header-list%3D%26q-url-param-list%3D%26q-signature%3Da08ad0ce8b6692f2144f367e2cebfcc4ff93ab36&token=302236e762a85aafddcc9a9d71e1cbd29438213c10001&clientIP=116.112.6.178&clientUA=704930d2-6d79-4544-9e4f-f21e85e86f58',
	                    type: 'video/mp4'
	                }],
	                options: {
	                    autoplay: false,
	                    volume: 0.6,
	                    poster: 'https://video-1253468238.cos.ap-beijing.myqcloud.com/%E7%89%87%E5%A4%B4%402x.jpg?sign=q-sign-algorithm%3Dsha1%26q-ak%3DAKIDGAAwlA6t4ozESDD2ALGeCahmNpv5kpO5%26q-sign-time%3D1520922820%3B1520924620%26q-key-time%3D1520922820%3B1520924620%26q-header-list%3D%26q-url-param-list%3D%26q-signature%3D754928637a1a73b96a50b98ef66ef4f62d9019e0&token=342118631f9bac4e01c0e328268b819de076b6ed10001&clientIP=116.112.6.178&clientUA=4a674910-e55d-45fe-9097-fa70dec7f77a'
	                }
	            }
			}
		},
		components:{
			myVideo
		},
		methods:{
		    showSummary:function(teacher){
		      this.isZhankai = !this.isZhankai;
		      if(this.isZhankai){
		        this.btnTitle = "收起简介";
		      }else{
		        this.btnTitle = "展开简介";
		      }
		    }
		},
		created(){
			this.$http.get('./../static/teachers.json')
		  		.then(function(data){
		  			// console.log(data.json());
		        	return data.json(); //json解析
		  		})
		      .then(function(data){
		        this.teachers = data;
		      })
		}
	}
</script>

<style scoped>
	article{
		padding: 20px 0 40px 0;
		box-sizing: border-box;
	}
	.panel{
		background-color:transparent;
		border: none;
		box-shadow: none;
		margin-bottom: 5px;
		max-width: 1200px;
		margin: 0 auto;
		padding:0;
	}
	.panel-body{
		border: none;
		/*margin-top: 30px;*/
	}
	.titleContainer{
		margin-top: 60px;
		margin-left: 5px;
	}
	.class-title{
		margin: 10px 0 0 0;
		color: #555;
	    line-height: 1.5;
	    padding: 10px 0;
	    font-size: 16px;
	}
	.des{
		color: #888;
		font-size: 14px;
		margin-bottom: 5px;
	}
	.btn{
		border:none;
		background: none;
		color: #3ea9f5;
	}
	.video-container{
		margin: 20px auto;
		padding: 0;
	}
	
	.thumbnail{
		border-radius: 6px;
		border: none;
		/*box-shadow: 0px 2px 4px #aaaaaa;*/
		padding: 30px;
	}
	.row{
		border: none;
		margin-bottom: 40px;
		box-shadow: none;
		padding: 20px 20px;
		text-align: center;
	}
	.iconfont-h2{
		/*font-size: 36px;*/
		font-family: iconfont;
		color: red;
	}
	.teacher{
		border-radius: 10px;
	}
</style>


