<template>
  <div class="card text-dark bg-light mb-3">
      <div v-for="a in answers">
        <div class="panel panel-default">
            <!-- Default panel contents -->
            <div class="panel-heading" style="padding:5px;"><p style="color:#2278cf;margin:5px;font-size:18px;font-weight:300;letter-spacing:1.3px;">{{a.title}}</p></div>
            <div class="bs-glyphicons">
              <ul class="bs-glyphicons-list" style="padding:5px;">
                <li v-for="i in a.answers">
                  <p>{{i.id}}</p>
                  <p><strong>{{i.a}}</strong></p>
                </li>
              </ul>
            </div>
        </div>
      </div>

  </div>
</template>

<script>
  export default{
    name:"answer77902",
    data(){
      return{
        answers:[]
      }
    },
    created(){
      this.$http.get('./../static/ANS77902.json')
          .then(function(data){
            console.log(data.json());
              return data.json(); //json解析
          })
          .then(function(data){
            this.answers = data;
          })
    }
  }
</script>





