<template>
  <div id="app">
    <blog-header id="#top"></blog-header>
    <router-view v-wechat-title='$route.meta.title'></router-view>
    <blog-footer></blog-footer>
  </div>
</template>

<script>
import AddBlog from './components/AddBlog'
import ShowBlogs from './components/ShowBlogs'
import BlogHeader from './components/BlogHeader'
import BlogFooter from './components/BlogFooter'
import AboutUs from './components/AboutUs'

export default {
  name: 'app',
  components: {
    AddBlog,ShowBlogs,BlogHeader,BlogFooter,AboutUs
  }
}
</script>

<style>
	:root{
		--theme-color:#fff;
        /* #093f70; */    
        --theme-text-color:#555;
		--theme-rgba:rgba(9,63,112,0.9);
		--light-theme-color:#fff;
        --light-theme-text-color:#333;
        --light-theme-bgcolor:#f7f8f9;
		/* #074782; */
	}
	/*@font-face{
	    font-family: 'bichimel';
	    src: url('./../static/font/Bichimel.ttf');
	    font-weight: normal;
	    font-style: normal;
    }*/
    @font-face {
        font-family: 'iconfont';  /* project id 544540 */
        src: url('//at.alicdn.com/t/font_544540_1prnlf3g163nxw29.eot');
        src: url('//at.alicdn.com/t/font_544540_1prnlf3g163nxw29.eot?#iefix') format('embedded-opentype'),
        url('//at.alicdn.com/t/font_544540_1prnlf3g163nxw29.woff') format('woff'),
        url('//at.alicdn.com/t/font_544540_1prnlf3g163nxw29.ttf') format('truetype'),
        url('//at.alicdn.com/t/font_544540_1prnlf3g163nxw29.svg#iconfont') format('svg');
    }
    *{
        box-sizing: border-box;
    }
    html,body{
    	margin:0;
    	padding:0;
    	width:100%;
    	height: 100%;
        background: var(--light-theme-bgcolor);
    	overflow:scroll;
	    overflow-y:auto;
	    overflow-x: hidden;
	    font-family: NotoSansHans-Regular,AvenirNext-Regular,arial,"Microsoft Yahei","Hiragino Sans GB","WenQuanYi Micro Hei",sans-serif;
        /*'Segoe UI',Tahoma,Geneva,Verdana;*/
    }

	#app{
		margin:0;
		/*padding-right等于footer的高度*/
	    padding: 0 0 220px 0;
	    line-height: 1.5;
	    page-break-before: auto;
	    page-break-after: always;
	    min-height: 100%;
	    height: auto !important;
	    height:100%;
	    min-height: 100%;
	    position: relative;
	}
  .title{
    color: #555;
    font-size: 22px;
    /*font-weight: bold;*/
    text-align:left;
    padding-bottom: 5px;
    margin-left: 0;
    border-bottom: 3px solid #3ea9f5;
  }
	.btn{
	    border: none;
	    border-radius: 4px;
	    padding: 5px 10px;
	    margin-top: 10px;
	    text-align: center;
	    -webkit-transition-duration: 0.4s; /* Safari */
	    transition-duration: 0.4s;
	    text-decoration: none;
	    overflow: hidden;
	    cursor: pointer;
	    outline: none;
    }
    .btn-primary{
		background-color: #2278cf;
		color: #FFFFFF;
    }
    article{
	    line-height: 2;
	    margin:0px;
	    font-size: 16px;
		}
    .content{
            padding: 0 20px;
            background: #fff;
    }
    .panel{
            border-radius: 6px;
    }
    .iconfont{
        font-family:"iconfont" !important;
        font-size:16px;font-style:normal;
        -webkit-font-smoothing: antialiased;
        -webkit-text-stroke-width: 0.2px;
        -moz-osx-font-smoothing: grayscale;
    }
</style>



